import {
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CTable,
  CTableHead,
  CTableRow,
  CTableBody,
  CTableDataCell,
  CTableHeaderCell,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";

import EDU_AXIOS from "src/helpers/edu_axios";

const get_class = (props) => {
  let school_id = props.match.params.school_id;
  let [gp, setGp] = useState();

  useEffect(() => {
    EDU_AXIOS.get(`${process.env.REACT_APP_API_PATH}/cm/get-class/${school_id}`)
      .then((response) => {
        setGp(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);
  let Record = () => {
    let el = [];
    if (!!gp && gp.groups.length > 0) {
      for (let i = 0; i < gp.groups.length; i++) {
        let row = [];
        row.push(
          <CTableRow key={gp.groups[i].group_name}>
            <CTableHeaderCell scope="row">
              {gp.groups[i].group_name}
            </CTableHeaderCell>
          </CTableRow>
        );
        for (let j = 0; j < gp.groups[i].gp_classes.length; j++) {
          row.push(
            <CTableRow key={gp.groups[i].gp_classes[j]}>
              <CTableDataCell>{gp.groups[i].gp_classes[j]}</CTableDataCell>
            </CTableRow>
          );
        }
        el.push(row);
      }
    } else {
      el.push(
        <CTableRow>
          <CTableDataCell scope="row" style={{ textAlign: "center" }}>
            No record found...
          </CTableDataCell>
        </CTableRow>
      );
    }

    return el;
  };

  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="6" xl="8">
              <h5>View Classes</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Get all classes</p>
          <CTable striped>
            <CTableHead>
              <CTableRow>
                <CTableHeaderCell scope="col">Classes</CTableHeaderCell>
              </CTableRow>
            </CTableHead>

            <CTableBody>{Record()}</CTableBody>
          </CTable>
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
};

export default get_class;
